header {
  height: 8vh;
  background: white;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  justify-content: space-around;
  flex-wrap: wrap;
}

.logo_container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.logo_container > img {
  width: 2.5rem;
}

header > label {
  display: flex;
  gap: 1rem;
  background: white;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  border: 1px solid blue;
}

header > label > input {
  border: none;
}

header > label > input:active {
  outline: none;
}

footer {
  background-color: lightgray;
  display: flex;
  justify-content: center;
  padding: 2rem;
  gap: 2rem;
  flex-direction: column;
  align-items: center;
  bottom: 0;
  /* position: fixed; */
  width: 100%;
}

footer > nav {
  display: flex;
  gap: 1rem;
}

/* ---------------------------------------------- 아이템 컨테이너 안에 아이템 */

.item_card {
  flex-basis: 25%;
  padding: 15px;
}

.item_card .item_card_title {
  width: 14vw;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
}

.item_card img {
  margin-bottom: 10px;
  width: 14vw;
  height: 20vh;
  object-fit: cover;
}

/* ---------------------------------------------- 아이템 컨테이너 */
.items_container {
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 1;
  flex-grow: 2;
  flex-basis: 60%;
  border-radius: 10px;
  background-color: white;
}

main {
  /* overflow-y: scroll; */
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center;
}

.main_container {
  display: flex;
  padding: 16px;
  height: 100%;
  background-color: rgb(233, 233, 233);
  width: 80vw;
  margin-top: 5vh;
  border-radius: 20px;
}

/* ---------------------------------------------- 슬라이더 */
.slider_container {
  height: 1vh;
}

.slider {
  margin-top: 2vh;
  width: 80vw;
  height: 20vw;
  text-align: center;
  border-radius: 20px;
}

.slick-prev::before,
.slick-next::before {
  background-color: rgb(0, 0, 0);
  border-radius: 100%;
}

/* ---------------------------------------------- 카테고리 */

.category_tab {
  flex-shrink: 0;
  flex-grow: 1;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: x-large;
  width: 5vw;

  /* background-color: rgb(197, 197, 197); */
}

.tab_item {
  display: flex;
  gap: 16px;
  align-items: center;
  padding: 8px;
  border-radius: 8px;

  /* background-color: gray; */
}

.tab_item img {
  width: 50px;
  border-radius: 100%;
}

.tab_item:hover {
  background-color: rgb(0, 189, 60);
  font-weight: bold;
}
